<template>
    <div class="barang">
        <h3 class="mb-10">Data Barang</h3>
        <vx-card>
            <vs-table
              :data="barang"
               pagination search>
                <template slot="header">
                    <vx-tooltip text="Tambah Barang" style="display:inline">   
                        <vs-button @click="addBarang = true" class="float-right mb-3" color="#0fabbc" icon="add_circle_outline"> Barang</vs-button>
                    </vx-tooltip>
                </template>
                <template slot="thead" >
                    <vs-th sort-key="sku" class="bg-primary text-white text-center">
                    SKU
                    </vs-th>
                    <vs-th sort-key="deskripsi" class="bg-primary text-white text-center">
                    Deskripsi
                    </vs-th>
                    <vs-th class="bg-primary text-white text-center">
                    Aksi
                    </vs-th>
                </template>

                <template slot-scope="{data}">
                    <vs-tr :data="item" :key="index" v-for="(item, index) in data" >
                    <vs-td class="text-center" style="width:10%;">{{item.tglRequest}}</vs-td>
                    <vs-td class="text-center" style="width:10%;">{{item.tglKirim}}</vs-td>
                    <vs-td style="width:55%;">{{item.isi}}</vs-td>
                    <vs-td class="text-center" style="width:10%;">{{item.countNumber}}</vs-td>
                    <vs-td class="text-center" style="width:5%;"><vs-button color="#0fabbc" size="small" icon="pageview" ></vs-button></vs-td>
                    </vs-tr>
                </template>
            </vs-table>

            <!-- Tambah Barang -->
            <vs-popup class="holamundo"  title="Tambah Barang" :active.sync="addBarang">
                <Create />
            </vs-popup>
        </vx-card>
    </div>
</template>

<script>
import Create from './create.vue'
import Edit from './edit.vue'

export default {
    name: 'barang',
    data() {
        return {
            barang: [],
            addBarang: false
        }
    },
    components: {Create,Edit}
}
</script>